<template>
  <view class="chat-message" :class="{'user-message': isUser, 'ai-message': !isUser}">
    <!-- 头像 -->
    <view class="message-avatar">
      <image 
        v-if="isUser" 
        src="/static/avatars/user.png" 
        class="avatar-img"
        mode="aspectFit"
      />
      <image 
        v-else 
        src="/static/avatars/ai.png" 
        class="avatar-img"
        mode="aspectFit"
      />
    </view>

    <!-- 消息内容 -->
    <view class="message-content">
      <!-- 消息头 -->
      <view class="message-header">
        <text class="sender-name">{{ isUser ? '您' : '粤风AI助手' }}</text>
        <text class="message-time">{{ formatTime(message.timestamp) }}</text>
      </view>

      <!-- 消息体 -->
      <view class="message-body">
        <!-- 用户消息 -->
        <template v-if="isUser">
          <text class="message-text" v-if="!isEditing">{{ message.content }}</text>
          <textarea
            v-else
            v-model="editContent"
            class="message-edit-input"
            auto-height
            :focus="isEditing"
            @blur="saveEdit"
          />
          <view class="message-actions">
            <view class="action-btn" @tap="startEdit" v-if="!isEditing">
              <uni-icons type="compose" size="16" color="#64748b" />
              <text>编辑</text>
            </view>
            <view class="action-btn" @tap="copyMessage(message.content)">
              <uni-icons type="paperclip" size="16" color="#64748b" />
              <text>复制</text>
            </view>
            <view class="action-btn" @tap="saveEdit" v-if="isEditing">
              <uni-icons type="checkmark" size="16" color="#10b981" />
              <text>保存</text>
            </view>
            <view class="action-btn" @tap="cancelEdit" v-if="isEditing">
              <uni-icons type="close" size="16" color="#ef4444" />
              <text>取消</text>
            </view>
          </view>
        </template>

        <!-- AI消息 -->
        <template v-else>
          <text class="message-text">{{ message.content }}</text>
          
          <!-- 附件显示 -->
          <view v-if="message.attachments && message.attachments.length" class="attachments">
            <view 
              v-for="(attachment, idx) in message.attachments" 
              :key="idx"
              class="attachment-item"
            >
              <uni-icons type="paperclip" size="14" color="#64748b" />
              <text class="attachment-name">{{ attachment.name }}</text>
            </view>
          </view>

          <!-- AI消息操作 -->
          <view class="message-actions">
            <view 
              class="action-btn" 
              :class="{'active': message.feedback === 'like'}"
              @tap="handleFeedback('like')"
            >
              <uni-icons 
                :type="message.feedback === 'like' ? 'hand-thumbsup-filled' : 'hand-thumbsup'" 
                size="16" 
                :color="message.feedback === 'like' ? '#10b981' : '#64748b'" 
              />
              <text>有用</text>
            </view>
            <view 
              class="action-btn" 
              :class="{'active': message.feedback === 'dislike'}"
              @tap="handleFeedback('dislike')"
            >
              <uni-icons 
                :type="message.feedback === 'dislike' ? 'hand-thumbsdown-filled' : 'hand-thumbsdown'" 
                size="16" 
                :color="message.feedback === 'dislike' ? '#ef4444' : '#64748b'" 
              />
              <text>无用</text>
            </view>
            <view class="action-btn" @tap="copyMessage(message.content)">
              <uni-icons type="paperclip" size="16" color="#64748b" />
              <text>复制</text>
            </view>
          </view>
        </template>
      </view>
    </view>
  </view>
</template>

<script setup>
import { ref } from 'vue'

const props = defineProps({
  message: {
    type: Object,
    required: true
  }
})

const emit = defineEmits(['feedback', 'edit'])

const isUser = computed(() => props.message.role === 'user')
const isEditing = ref(false)
const editContent = ref('')

const formatTime = (timestamp) => {
  const date = new Date(timestamp)
  return `${date.getHours().toString().padStart(2, '0')}:${date.getMinutes().toString().padStart(2, '0')}`
}

const startEdit = () => {
  isEditing.value = true
  editContent.value = props.message.content
}

const saveEdit = () => {
  if (editContent.value.trim() && editContent.value !== props.message.content) {
    emit('edit', props.message.id, editContent.value)
  }
  isEditing.value = false
}

const cancelEdit = () => {
  isEditing.value = false
  editContent.value = ''
}

const copyMessage = async (content) => {
  try {
    // #ifdef H5
    await navigator.clipboard.writeText(content)
    // #endif
    // #ifdef APP-PLUS || MP
    uni.setClipboardData({
      data: content,
      success: () => {
        uni.showToast({
          title: '已复制到剪贴板',
          icon: 'success'
        })
      }
    })
    // #endif
  } catch (error) {
    console.error('复制失败:', error)
  }
}

const handleFeedback = (type) => {
  emit('feedback', props.message.id, type)
}
</script>

